@import "https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Montserrat:wght@400;700&display=swap";
body {
  background: linear-gradient(to bottom right, #1e97ca, #b0bec5);
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Lato, sans-serif;
  display: flex;
}

.calculator {
  background: #696969;
  border: 1px solid #ffffff1a;
  border-radius: 16px;
  padding: 1.2rem;
  box-shadow: 0 8px 25px #00000080, 0 0 15px #0003;
}

.history {
  color: #80deea;
  text-align: right;
  opacity: .7;
  height: 1.5rem;
  margin-bottom: .3rem;
  font-family: Montserrat, sans-serif;
  font-size: .9rem;
  overflow: hidden;
}

.display {
  color: #e0e0e0;
  text-align: right;
  letter-spacing: .05em;
  background: #000;
  border-radius: 8px;
  margin-bottom: .8rem;
  padding: .6rem;
  font-family: Montserrat, sans-serif;
  font-size: 2.5rem;
}

.button-grid {
  grid-template-columns: repeat(4, 5rem);
  grid-auto-rows: 5rem;
  gap: .7rem;
  display: grid;
}

.button {
  cursor: pointer;
  border: none;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  transition: background-color .2s, transform .1s;
  display: flex;
}

.button:active {
  transform: translateY(1px);
}

.digit {
  color: #e0e0e0;
  background: #424242;
}

.digit:hover {
  background: #616161;
}

.zero {
  grid-column: span 2;
}

.operator {
  color: #fff;
  background: #03a9f4;
}

.operator:hover {
  background: #29b6f6;
}

.clear {
  color: #fff;
  background: #f44336;
  grid-column: span 2;
  font-size: 1.3rem;
}

.clear:hover {
  background: #e57373;
}

.equals {
  color: #fff;
  background: #4caf50;
  grid-row: span 2;
}

.equals:hover {
  background: #66bb6a;
}

.footer {
  text-align: center;
  color: #bdbdbd;
  margin-top: 1.5rem;
  font-family: Lato, sans-serif;
  font-size: .75rem;
}

.footer a {
  color: #03a9f4;
  font-weight: bold;
  text-decoration: none;
  transition: color .2s;
}

.footer a:hover {
  color: #80deea;
}
